@font-face {
  font-family: "Quicksand";
  src: url("./fonts/Quicksand-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Quicksand";
  src: url("./fonts/Quicksand-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: "Permanent Marker";
  src: url("./fonts/PermanentMarker-Regular.ttf");
  font-weight: normal;
  font-style: cursive;
}

html {
  font-size: 22px;
}

body {
  font-size: 22px;
  font-family: "Quicksand", sans-serif;
  color: $black;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Quicksand", sans-serif;
  font-weight: 700;
  margin-bottom: 15px;
}

h1 {
  font-size: 46px;
  margin-bottom: 26px;
  text-transform: uppercase;
}

h2 {
  font-size: 36px;
  margin: 0 0 20px;
}

h3 {
  font-size: 30px;
  margin-bottom: 18px;
}

h4 {
  font-size: 24px;
  margin-bottom: 18px;
}

a {
  text-decoration: none;
  color: $purple;
}

p a,
h2 a,
li a {
  color: $purple;
  transition: all 0.3s;
  &:hover {
    color: $black;
  }
}

p {
  font-size: 22px;
  margin: 0 0 20px;
  line-height: 1.4;
}

ul,
ol {
  margin: 0 0 20px;
  line-height: 1.4;
  li {
    margin-bottom: 10px;
  }
}

ul {
  list-style-type: circle;
}

nav {
  font-family: "Quicksand", sans-serif;
  font-size: 16px;
}

button,
input,
optgroup,
select,
textarea {
  font-family: "Quicksand", sans-serif;
}

blockquote {
  margin: 0 0 20px;
  padding: 5px 20px;
  border-left: 3px solid $purple;
  background: #eee;
  font-style: italic;
  p:last-child {
    margin: 0;
  }
}

::selection {
  background: $grey; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: $grey; /* Gecko Browsers */
}
