.hide {
  text-indent: -999999px;
  font-size: 0;
}

.avatar {
  min-width: 50px;
  height: 50px;
  border-radius: 50%;
  align-self: center;
}

pre.error {
  padding: 5rem;
  background: $white;
  line-height: 1.8;
  font-size: 18px;
  white-space: pre-line;
}

.homepage {
  .pro-box {
    background-color: #eee;
    padding: 20px 20px 10px;
    width: 60%;
    margin: 30px auto 40px;
    h3 {
      color: #aaa;
      margin: 0 0 10px;
    }
    h2 {
      margin: 10px 0 20px;
    }
    p {
      margin-left: 20px;
    }
    @media all and (max-width: 700px) {
      width: 90%;
    }
  }
}

.no-rights {
  .border {
    margin: 70px 0;
    height: 40px;
    width: 100%;
    background: linear-gradient(
      135deg,
      black 33%,
      yellow 33%,
      yellow 66%,
      black 66%
    );
    // background-repeat: no-repeat;
    background-size: 10% 100%;
  }
}
